'use strict';
import { CHOOSEBUSSINESS ,APPT_MEASUREHOME_SUCCESS} from '@/config/router.js';
import simpleAddress from "@/components/simple-address-normal/simple-address.nvue" 
import { measureHome} from '@/api/measureHome.js';
import { loadCity } from '@/api/city.js';
var _self;
export default {
	components: {
	            simpleAddress
				
	        },
	
	data() {
		return{
			cityPickerValueDefault: [0, 0, 1],
			                pickerText: '',
							apptArray: ['中国', '美国', '巴西', '日本'],
							apptIndex: 0,
							dataList:{},
							periodArray: [],
							periodIndex: 0,
							areaShow:false,
							budgetShow:false
							
		}
	},
	methods:{
		areaInput(e){
			// console.log(e.detail)
			// this.$forceUpdate();
			if(e.detail.value ==""){
				_self.areaShow=false
			} else{
				_self.areaShow=true
			}
			
		},
		budgetInput(e){
			// console.log(e.detail)
			// this.$forceUpdate();
			if(e.detail.value ==""){
				_self.budgetShow=false
			} else{
				_self.budgetShow=true
			}
		},
		openAddres() {
			
		                this.$refs.simpleAddress.open();
		            },
		            onConfirm(e) {
						
						var b=e.label.split('-')
						// console.log(e)
						
		                this.pickerText = b[0]+' '+ b[1]+' '+b[2]
		            },
		open(){
		         this.$refs.popup.open()
		      },
		cancel() {
			this.$refs.popup.close()
		},
		change(e) {
			console.log('是否打开:' + e.show)
		},
		apptBindPickerChange: function(e) {
		            console.log('picker发送选择改变，携带值为', e.target.value)
		            this.apptIndex = e.target.value
		        },
		periodBindPickerChange: function(e){
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.periodIndex = e.target.value
			_self.dataList.address = this.periodArray[e.target.value]
		},
		toast(v){
			
				uni.showToast({
				    title: v,
				    duration: 2000,
					icon:'none'
				});
				
			
		},
		
		 toLinkChoose(){
			 // _self.dataList.address = this.pickerText
			 if(!this.dataList.name){
			 	this.toast('请输入您的姓名')
			 	return;
			 }
			 if(!this.dataList.address){
			 	this.toast('请选择你所在城市')
			 	return;
			 }
			 if(!this.dataList.communityName){
			 	this.toast('请输入您的小区名称')
			 	return;
			 }
			 if(!this.dataList.area){
			 	this.toast('请输入您的房屋面积')
			 	return;
			 }
			 if(!this.dataList.budget){
			 	this.toast('请输入您的装修预算')
			 	return;
			 }
			 if(!this.dataList.communityName){
			 	this.toast('请输入您的房屋面积')
			 	return;
			 }
			 if(!this.dataList.phone){
			 	this.toast('请输入您的手机号')
			 	return;
			 }
			 measureHome (this.dataList).then(res=>{
				 console.log(res)
				
				 uni.showToast({
				 								title: "提交成功",
				 								icon: 'success',
				 								duration: 2000,
				 							});
											
				 							uni.navigateTo({
				 								url:`${CHOOSEBUSSINESS}?id=${this.dataList.address}&uuid=${res.uuid}`  
				 							})
				 // uni.navigateTo({
				 // 	url:APPT_MEASUREHOME_SUCCESS
				 // })
			 })
			
		}
	},
	async onLoad() {
		
		_self =this
		let e= await loadCity()
		console.log(e)
		_self.periodArray = e.list.map(res=>{
			return res.name
		})
		
		_self.dataList.address = _self.periodArray[0]
	}
};
